html,
body {
   height: 100%;
}

#root {
   height: 100%;
}

.App {
   height: 100%;
}

.mainBox {
   -webkit-box-shadow: -1px 7px 49px -28px rgba(0, 0, 0, 1);
   -moz-box-shadow: -1px 7px 49px -28px rgba(0, 0, 0, 1);
   box-shadow: -1px 7px 49px -28px rgba(0, 0, 0, 1);
}

.error404 {
   width: 657px;
   height: 372px;
}

.hxtQOl {
   margin: 0px;
}

.exactt-btn {
   padding-left: 8;
   padding-right: 8;
   height: 32;
   display: flex;
   align-items: center;
   font-weight: 400;
   cursor: pointer;
}

.exactt-btn:hover {
   text-decoration: underline !important;
}

.label {
   font-weight: 500;
}

.scroll-view::-webkit-scrollbar {
   -webkit-appearance: none;
   width: 0px;
   height: 5px;
}

.scroll-view::-webkit-scrollbar-thumb {
   border-radius: 2px;
   background-color: rgba(0, 0, 0, 0.2);
   -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

@media all {
   .page-break {
      display: none;
   }
}

@media print {
   html,
   body {
      height: initial !important;
      overflow: initial !important;
      -webkit-print-color-adjust: exact;
      font-size: 12px;
   }
}

@media print {
   .page-break {
      margin-top: 1rem;
      display: block;
      page-break-before: always;
   }
}

@page {
   size: auto;
   margin: 5mm;
}
